@import "mixins";
@import "vars";
@import "reset";

html {
    font-size: 62.5%;
}

body {
    background-color: #FAEBD7;

    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 1.4rem;
    line-height: 1.5;

}

.container {
    max-width: 110rem;
    margin: 0 auto;
    padding-right: 5rem;
    padding-left: 5rem;
}

.navision {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10rem;
}

.navision__img {
    width: 20rem;
}
.navision__list {
    height: 4rem;
    align-items: center;
    display: flex;
    list-style-type: none;
    text-shadow: 0 6px 8px rgba($red, .2);

    @media (max-width: 768px) {
        display: none;
    }

    a {
        text-decoration: none;
        color: inherit;
    }
}

.navision__item {
    margin-right: 4rem;
    color: $red;

    font-size: 1.5rem;
    text-transform: uppercase;
    &:last-child {
        margin: 0;
    }

    &:hover {
        transform: scale(1.5);

    }
}
.about {
    text-align: center;
    color: $red;

    p {
        margin-bottom: 4rem;
        font-size: 2.3rem;
    }

}

.about__title {
    margin-bottom: 4rem;
    font-size: calc(30px + 20 * ((100vw - 320px) / (1180 - 320)));
}

.btn {
    display: inline-block;
    vertical-align: top;
    padding: 1.12em 2.1em;

    background: $secondary;
    border: none;
    cursor: pointer;
    border-radius: 3em;

    font-family: inherit;
    font-size: 1.2rem;
    line-height: 1.1;
    font-weight: 500;
    color: $white;
    text-decoration: none;

    transition: background-color .2s linear;

    &:hover {
        text-decoration: none;
    }

    &--red {
        background-color: $red;
        box-shadow: 0 7px 17px -10px rgba($red, .3);

        &:hover,
        &:focus {
            background-color: darken($red, 10%);
        }
    }
}
